.main-login-component {
  height: 100%;
  min-height: 100vh;
}

.intro-image-div {
  border-radius: 0px 20px 20px 0px;
  background: var(
    --new,
    linear-gradient(130deg, #492fb2 -1.74%, #adadad 108.02%)
  );
  background-image: url("../../../assets/images/new-banner.png");
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  width: 40%;
}

.login-form-div {
  width: 60%;
  h1 {
    font-family: Urbanist;
    font-size: 40px;
    font-style: normal;
    font-weight: 500;
    line-height: 130%;
    /* 52px */
    background: var(--2, linear-gradient(90deg, #222 0%, #424242 100%));
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  h4 {
    color: var(--Neutral-700, #7e838b);
    font-family: Urbanist;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 0;
  }
}

form {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-top: 20px;
  label {
    color: var(--Text-Dark, #212121);
    font-family: Urbanist;
    font-size: var(--Font-Size-2, 14px);
    font-style: normal;
    font-weight: 500;
    line-height: var(--Line-Heights-10, 20px);
    /* 142.857% */
    letter-spacing: var(--Letter-Spacing-2, 0.1px);
    margin-bottom: 4px;
  }

  input[type="email"],
  input[type="password"],
  input[type="text"] {
    padding: 12px;
    border-radius: 8px;
    border: 1px solid #ccc;
    font-size: 14px;
    width: 100%;
    box-sizing: border-box;
  }

  .password-input {
    position: relative;

    input {
      width: 100%;
      padding-right: 40px;
    }

    .eye-button {
      position: absolute;
      right: 10px;
      top: 50%;
      transform: translateY(-50%);
      background: transparent;
      border: none;
      cursor: pointer;
      padding: 0;

      img {
        width: 20px;
      }
    }
  }

  .forgot {
    text-align: right;
    margin-top: -8px;

    a {
      color: #3b27b9;
      font-size: 14px;
      text-decoration: none;
    }
  }
  .login-btn {
    border-radius: 8px;
    border: 1px solid;
    background: #222;
    color: var(--Text-Light, #fff);
    text-align: center;
    font-family: Urbanist;
    font-size: var(--Font-Size-3, 16px);
    font-style: normal;
    font-weight: 500;
    line-height: var(--Line-Heights-9, 24px); /* 150% */
    letter-spacing: var(--Letter-Spacing-1, 0.15px);
    padding: 12px;
  }
  .error-message {
    color: red;
  }
  .login-btn:disabled {
    padding: 12px;
    border: none;
    border-radius: 8px;
    background-color: #a0a0a0 !important;
    color: #fff;
    font-size: 16px;
    cursor: not-allowed;
  }

  .signup-text {
    font-size: 14px;
    text-align: center;

    a {
      color: #3b27b9;
      text-decoration: none;
      font-weight: 500;
    }
  }

  .warning-box {
    display: flex;
    align-items: center;
    background-color: #fff4db;
    border-radius: 8px;
    padding: 12px;
    gap: 8px;
    color: #333;

    p {
      color: #a16800;
      font-family: Urbanist;
      font-size: 12px;
      font-style: normal;
      font-weight: 500;
      line-height: 18px; /* 150% */
      margin: 0;
    }
  }
}
.otp-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.otp-inputs {
  display: flex;
  gap: 10px;
  margin: 20px 0;
}

.otp-inputs input {
  width: 50px;
  height: 50px;
  font-size: 1.5rem;
  text-align: center;
  border: 2px solid #ccc;
  border-radius: 8px;
  outline: none;
  transition: border-color 0.2s;
}

.otp-inputs input:focus {
  border-color: black;
}

.login-btn {
  width: 100%;
  background: black;
  color: white;
  padding: 10px;
  border: none;
  font-size: 1rem;
  border-radius: 8px;
  cursor: pointer;
}

.timer {
  color: #3366ff;
}
.password-wrapper {
  position: relative;
  display: flex;
  align-items: center;
}

.password-wrapper input {
  flex: 1;
  padding-right: 40px; /* space for the button */
}

.eye-btn {
  position: absolute;
  right: 10px;
  background: none;
  border: none;
  font-size: 1.2rem;
  cursor: pointer;
}



/* Default (desktop) already good */

/* Tablets */
@media (max-width: 991px) {
  .main-login-component {
    flex-direction: column;
  }

  .intro-image-div {
    width: 100%;
    height: 60vh;
    border-radius: 0 0 20px 20px;
  }

  .login-form-div {
    width: 100%;
    padding: 30px 0px !important;
  }

  .login-form-div h1 {
    font-size: 32px;
  }

  .login-form-div h4 {
    font-size: 14px;
  }

  form {
    gap: 12px;
  }

  .otp-inputs input {
    width: 40px;
    height: 40px;
    font-size: 1.2rem;
  }
}

/* Mobile (smaller than 576px) */
@media (max-width: 576px) {
  .intro-image-div {
    height: 60vh;
    border-radius: 0 0 15px 15px;
  }

  .login-form-div {
    padding: 20px 0px !important;
  }

  .login-form-div h1 {
    font-size: 26px;
    text-align: center;
  }

  .login-form-div h4 {
    font-size: 13px;
    text-align: center;
  }

  input[type="email"],
  input[type="password"],
  input[type="text"] {
    padding: 10px;
    font-size: 13px;
  }

  .login-btn {
    font-size: 14px;
    padding: 10px;
  }

  .otp-inputs {
    gap: 6px;
  }

  .otp-inputs input {
    width: 30px;
    height: 30px;
    font-size: 1rem;
  }

  .warning-box {
    font-size: 11px;
    padding: 10px;
  }

  .password-wrapper input {
    padding-right: 35px;
  }

  .eye-btn svg {
    width: 16px;
    height: 16px;
  }
}
